/* ----------------------------------------------------------------------------
 * Helpers
 * ------------------------------------------------------------------------- */

.bold {
  font-weight: 700;
}

.italic {
  font-style: italic;
}

.text-hide {
  background-color: transparent;
  border: 0;
  color: transparent;
  font: 0/0 a;
  text-shadow: none;
}

.no-padding {
  padding: 0 !important;
}

.no-margin {
  margin: 0 !important;
}

.white-bg {
  background: $white;
}

.black-bg {
  background: $black;
}

.light-grey-bg {
  background-color: $lighter-grey;
}

.grey-bg {
  background-color: $grey;
}

.eltdf-btn.eltdf-btn-solid:not(.eltdf-btn-custom-hover-bg):hover{
  color:$cinn !important;
  background-color: $gold !important;
}

// btn dark blue
.eltdf-btn.eltdf-btn-solid:not(.eltdf-btn-custom-hover-bg).et-btn-dk-blue:hover{
  color:$white !important;
  background-color: $dark-blue !important;
}

.eltdf-btn.eltdf-btn-solid:not(.eltdf-btn-custom-hover-bg).et-btn-dk-grey:hover{
  color:$white !important;
  background-color: $dark-grey-hover !important;
}

.eltdf-btn.eltdf-btn-solid:not(.eltdf-btn-custom-hover-bg).et-btn-dk-red:hover{
  color:$white !important;
  background-color: $dark-red !important;
}

.et-license-list{

  li{
    list-style: none;
    padding-bottom:20px;
    position: relative;
    padding-left: 20px;

    &:before{
      position: absolute;
      top: 0;
      left: 0;
      content: "\f054";
      font-family: FontAwesome;
      font-size: 14px;
      color: $cinn;
    }
  }
}

.href-link:before {
  display: block;
  content: " ";
  margin-top: -215px;
  height: 200px;
  visibility: hidden;
}

.embed-responsive {
  position: relative;
  display: block;
  height: 0;
  padding: 0;
  //overflow: hidden;
  margin: 0 auto;
  text-align: center;

  .embed-responsive-item, iframe{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    borderr: 0;
  }
}

.embed-responsive-16by9{
  padding-bottom: 56.25%;
}

.et-outer-padding{
  padding: 120px 0;
}

.shadow-small-btn {
  -webkit-box-shadow: 0px 20px 45px -6px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0px 20px 45px -6px rgba(0, 0, 0, 0.2);
  box-shadow: 0px 20px 45px -6px rgba(0, 0, 0, 0.2);
}
.shadow-small {
  -webkit-box-shadow: 0 15px 35px rgba(50,50,93,.1),0 5px 15px rgba(0,0,0,.07);
  -moz-box-shadow: 0 15px 35px rgba(50,50,93,.1),0 5px 15px rgba(0,0,0,.07);
  box-shadow: 0 15px 35px rgba(50,50,93,.1),0 5px 15px rgba(0,0,0,.07);
}
.shadow-medium {
  -webkit-box-shadow: 0px 15px 75px -6px rgba(0,0,0,0.38);
  -moz-box-shadow: 0px 15px 75px -6px rgba(0,0,0,0.38);
  box-shadow: 0px 15px 75px -6px rgba(0,0,0,0.38);
}

.divider-bottom {
  border-bottom: 1px solid $light-grey;
}
.divider-bottom-top {
  border-top: 1px solid $light-grey;
  border-bottom: 1px solid $light-grey;
}
.divider-top {
  border-top: 1px solid $light-grey;
}

.img-responsive{
  display: block;
  max-width: 100%;
  height: auto;
}

.et-cat{
  display: inline-block;
  padding: 6px 15px;
  border-radius: 25px;
  margin: 0 auto;
  text-align: center;
  font-size: 11px;
  line-height: 11px;
  color: $white;
  font-weight: 600;
  text-transform: uppercase;

  &.cat-red{
    background-color: $cinn;
  }

  a{
    color: $white;
  }

  .box-two & {
    position: absolute;
    top: 25px;
    left: 25px;
  }
}
.circle-dot{
  position: relative;
  border-radius: 50%;
  width: 43px;
  height: 43px;
  margin-right: 15px;
  display: flex;
  align-items: center;
  justify-content: center;

  i{
    text-align: center;
    font-size: 20px;
  }

  @include breakpoint(tablet){
    width: 66px;
    height: 66px;
    margin-right: 20px;

    i{
      font-size: 36px;
    }

  }

}


// Global Seperator class
.et2017-seperator .eltdf-separator,
.et-dotted-divider{
    border: none;
    border-right: 0;
    border-left: 0;
    // Safari & Chrome need border-style and border-color \_()_/
    border-style: dotted;
    border-color: rgba(213, 126, 0, 1);
    border-image-source: url('./assets/images/dots.svg');
    border-image-slice: 20% 20%;
    border-image-repeat: space;
    border-width: 3px 0 0 0;
}
