/*------------------------------------*\
    Single Feature Blog Post
\*------------------------------------*/
.et-feature-container{
  padding: 10%;
  max-width: 750px;
  margin: 0 auto;
  @include breakpoint(tablet){
    padding: 15% 0 ;
  }
  @include breakpoint(laptop){
    padding: 10% 0 ;
    max-width: none;
  }
}

.et-feature-slide {
  display: flex;
  flex-direction: column;

  @include breakpoint(laptop){
    flex-direction: row-reverse;
  }

  .eltdf-pswt-image{
    position: relative;
    overflow: hidden;
    display: flex;
    flex:1 0 60%;
    justify-content: center;
    align-items: center;
    background-size: cover;
    background-position: center;
    min-height: 150px;
    
    @include breakpoint(tablet){
        min-height: 450px;
    }

    img {

      @include breakpoint(laptop){
        max-width: none;
        position: absolute;
      }
    }
  }

  .eltdf-pswt-content {
    background: #fff;
    flex: 1 0 40%;
  }

  .eltdf-pswt-content-inner{
    padding: 10% 10% 7%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include breakpoint(laptop){
      text-align: left;
      padding: 15% 10%;
      align-items: baseline;
    }
    @include breakpoint(desktop){
      padding: 15% 15% 11%;
    }
  }

  .eltdf-post-info-category {
    font-size: 13px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 20px;
    a{
      color: $black;
    }
  }

  .blog-feature-latest{
    color: $pink;
    font-size: 15px;
    text-transform: uppercase;
    font-weight: 600;
    display: block;
    margin-bottom: 15px;

    @include breakpoint(tablet){
        font-size: 18px;
    }
  }

  .eltdf-pswt-title {
    color: $dark-blue;
    font-weight: 600;
    font-size: 24px;
    margin-bottom: 20px;

    @include breakpoint(laptop){
      font-size: 26px;
    }

    @include breakpoint(desktop){
      font-size: 36px;
    }
  }

  .eltdf-pt-three-excerpt{
    margin-bottom: 30px;

    @include breakpoint(desktop){
      margin-bottom: 60px;
    }

    p{
      color: $dark-blue;
      font-size: 16px;

      @include breakpoint(laptop){
        font-size: 16px;
      }
      @include breakpoint(desktop){
        font-size: 18px;
      }
    }
  }

  .eltdf-pswt-info {
    margin-top: 19px;
  }

  .eltdf-pswt-info-section {
    position: relative;
    display: none;
    width: 100%;
    clear: both;
    border-top: 1px solid rgba(141,141,141,.4);
  }

  .eltdf-pswt-info-section-left {
    float: left;
  }

  .eltdf-pswt-info-section-right {
    float: right;
  }

  .eltdf-blog-like, .eltdf-post-info-date, .eltdf-post-info-comments-holder {
    display: inline-block;
    margin: 0 13px 0 0;
    padding: 9px 0 2px;
  }

  .eltdf-blog-like {
    &:before {
      content: 'W';
      position: relative;
      top: 1px;
      display: inline-block;
      vertical-align: text-bottom;
      font-family: linea-basic-10;
      font-size: 14px;
      line-height: 1;
      margin: 0 7px 0 0;
      color: $pink;
    }

    a{
      color: $white;
    }
  }

  .eltdf-post-info-date {

    &:before {
      content: 'b';
      position: relative;
      top: 1px;
      display: inline-block;
      vertical-align: text-bottom;
      font-family: linea-basic-10;
      font-size: 14px;
      line-height: 1;
      margin: 0 7px 0 0;
    }

    a{
    }
  }

  .eltdf-post-info-comments-holder {
    &:before {
      content: ',';
      position: relative;
      top: 1px;
      display: inline-block;
      vertical-align: text-bottom;
      font-family: linea-basic-10;
      font-size: 14px;
      line-height: 1;
      margin: 0 7px 0 0;
    }

    a{
    }
  }

  &.box-two{
    flex-direction: column;
    border-radius: 10px;
    overflow: hidden;

    .box-two-inner{
      flex-direction: column;

      @include breakpoint(laptop){
        flex-direction: row;
      }
      display: flex;
    }

    .eltdf-pswt-image{
      min-height: 250px;
    }

    .eltdf-pswt-content-inner{
      padding: 15% 10%;
      position: relative;
      @include breakpoint(tablet){
          padding: 10%;
      }
      @include breakpoint(laptop){
          padding: 20% 10%;
      }
    }

    .eltdf-pswt-title{
      margin-top: 10px;
      text-align: center;
      font-size: 24px;
      margin-bottom: 0;
      width: 100%;
      
      @include breakpoint(tablet){
          font-size: 36px;
      }
      @include breakpoint(laptop){
       font-size: 24px;
      }
      @include breakpoint(desktop){
        font-size: 36px;
      }
    }

    .eltdf-pt-three-excerpt{
      margin: 0 auto 30px;

      ul{
        position: relative;
      }

      li{
        list-style: none;
        padding-left: 20px;
        &::before{
          position: absolute;
          left: 0;
          content:"\f058";
          font-family: "FontAwesome";
        }

        @include first(1){
          &::before{
            color: #9e3f93;
          }
        }
        @include even(){
          &::before{
            color: #01b7b8;
          }
        }
        @include odd-between(3,5){
          &::before{
            color: #ff9900;
          }
        }
      }
    }

    .et-btn-round{
      margin: 0 auto;
    }
  }

}

.feature-box-two__outer{
  padding: 10%;
}